function eventHandler(event: PopStateEvent): any {
	return Router.route(null, event);
}

let hasStarted = false;

const Router = {
	route(data: any, event?: PopStateEvent): any {
		throw new Error('route requires implementation');
	},

	start() {
		window.addEventListener('popstate', eventHandler);
		hasStarted = true;
		this.route(null);
	},

	pushState(data: any, url?: string | URL | null): void {
		if (!hasStarted) {
			return;
		}

		window.history.pushState(data, '', url);
		this.route(data);
	},

	replaceState(data: any, url?: string | URL | null): void {
		if (!hasStarted) {
			return;
		}

		window.history.replaceState(data, '', url);
		this.route(data);
	},

	stop() {
		if (!hasStarted) {
			return;
		}

		window.removeEventListener('popstate', eventHandler);
		hasStarted = false;
	},
};

export default Router;
